import { defineComponent, h, ref, reactive, watchEffect } from 'vue'
import { useSuspense } from 'composable/components'
import useGHL from 'composable/integration/ghl'
import { useRoute, useRouter } from 'vue-router'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Index from './index.vue'

export default defineComponent({
  async setup(_, ctx) {
    const { genSuspense } = useSuspense()
    const refComponent = ref(null)
    const route = useRoute()
    const router = useRouter()
    const hideFooter = ref(true)
    const loading = ref({
      default: false,
      subAccount: false,
      pipeline: false,
      update: false
    })
    const { isUninstalled, setUninstall } = useGHL()

    const saveChanges = async () => {
      const ghlStatus = ctx.attrs.integrations.find(item => item.name.toLowerCase() === 'ghl')
      loading.value.update = true
      await refComponent.value.saveChanges(async (res) => {
        if (res === 'success') {
          if (ghlStatus.is_enable == 0) ghlStatus.is_enable = 1

          loading.value.update = false
          router.push({
            name: 'integration',
            query: {
              reload: 1
            }
          })
        } else {
          loading.value.update = false
        }
      })
    }

    const genSaveButton = () =>
      h(
        Button,
        {
          color: 'teal',
          disabled: loading.value.subAccount || loading.value.pipeline && (!refComponent?.value?.form?.pipeline || !refComponent?.value?.form?.subAccount),
          loading: loading.value.default || loading.value.update,
          onClick: saveChanges
        },
        {
          default: () => 'Save'
        }
      )

    const genComponent = () =>
      genSuspense(
        h(Index, {
          ref: refComponent,
          status: ctx?.attrs?.integrations
            ? [...ctx.attrs.integrations].find(item =>  (item.id === route.query.id) || (item.name.toLowerCase() === 'ghl'))
            : undefined
        })
      )

    // WatchEffect for debugging and reactivity
    watchEffect(() => {
      if (!!isUninstalled.value) {
        setUninstall(false)
        router.push({
          name: 'integration',
          query: {
            reload: 1
          }
        })
      }
      if (refComponent.value && refComponent.value.isInstalled !== undefined) {
        hideFooter.value = !refComponent.value.isInstalled
      }
      if (refComponent.value && refComponent.value.loading !== undefined) {
        loading.value = refComponent.value.loading
      }
    })

    const modalProps = reactive({
      modelValue: true,
      closeTo: {
        name: 'integration',
        query: {
          reload: 1
        }
      },
      size: 'xs',
      title: 'HighLevel Config',
      hideFooter: hideFooter
    })

    const modalSlots = reactive({
      default: () => genComponent(),
      footer: () => genSaveButton()
    })

    return () => h(Modal, modalProps, modalSlots)
  }
})
