<template>
  <div class="space-y-8 text-base">
    <template v-if="!isInstalled">
      <p>Use the button below to get started</p>
      <a
        :href="`https://app.gohighlevel.com/integration/6613db15527f84167dd61db5`"
        :class="[
          'bg-teal-04',
          'text-white px-6',
          'py-2 inline-flex',
          'items-center justify-center',
          'hover:opacity-90 rounded-lg'
        ]"
        target="_blank"
        @click="closePanel">
        Go to GHL App Marketplace
        <i class="material-icons icon-xs">launch</i>
      </a>
    </template>
    <form
      v-if="!!isInstalled"
      class="space-y-6"
      @submit.prevent="saveChanges">
      <div>
        <div
          v-if="status.location_id"
          class="flex bg-blue-lighten text-blue-04 gap-4 px-4 py-3 rounded-lg mb-4"
        >
          <div>
            <i class="material-icons">info</i>
          </div>
          <div>
            <p>
              Make sure to check the 'Allow Duplicate Opportunity' option under
              your
              <a
                class="text-blue-04 hover:text-blue-03 font-semibold"
                :href="`https://app.gohighlevel.com/v2/location/${status.location_id}/settings/company`"
                rel="noreferrer noopener"
              >Business Profile Settings</a
              >
              to ensure Opportunities from existing customers appear in GHL
            </p>
          </div>
        </div>
        <label>Sub Account</label>
        <Select
          v-model="form.subAccount._id"
          placeholder="Select Sub-Account"
          by="_id"
          name="name"
          :error="validation.subAccount.$invalid"
          :items="subAccounts"
          :loading="loading.subAccount || loading.update"
          :disabled="loading.default || loading.subAccount"
          max-height="200px"
          block
          no-border
          @change="handleChangeSub">
          <template #prepend>
            <div class="px-4 py-4 w-full">
              <Input
                v-model="query.searchSub"
                placeholder="Search Sub-Account"
                class="w-full"
                block
                icon>
                <template #icon>
                  <span class="material-icons">search</span>
                </template>
              </Input>
            </div>
          </template>
          <template #item="{ item }">
            <div
              :class="[
                item._id === form.subAccount._id ? 'text-teal-04' : '',
                'flex gap-x-2 items-center'
              ]">
              {{ item.name }}
              <span
                v-if="item._id === form.subAccount._id"
                class="material-icons text-lg text-teal-04">
                check_circle
              </span>
            </div>
          </template>
        </Select>
        <small
          class="text-red-base block mb-4"
          v-for="(error, errorIndex) of validation.subAccount.$errors"
          :key="errorIndex"
          >{{ error.$message }}.
        </small>
      </div>
      <div>
        <label>Pipeline</label>
        <Select
          v-model="form.pipeline.id"
          placeholder="Select Pipeline"
          by="id"
          name="name"
          :error="validation.pipeline.$invalid"
          :items="pipelines"
          :loading="loading.pipeline || loading.update"
          :disabled="!form.subAccount?._id || loading.default || loading.pipeline || loading.subAccount"
          max-height="200px"
          block
          no-border
          @change="handleChangePipe">
          <template #prepend>
            <div class="px-4 py-4 w-full">
              <Input
                v-model="query.searchPipe"
                placeholder="Search Pipeline"
                class="w-full"
                block
                icon>
                <template #icon>
                  <span class="material-icons">search</span>
                </template>
              </Input>
            </div>
          </template>
          <template #item="{ item }">
            <div
              :class="[
                item.id === form.pipeline.id ? 'text-teal-04' : '',
                'flex gap-x-2 items-center'
              ]">
              {{ item.name }}
              <span
                v-if="item.id === form.pipeline.id"
                class="material-icons text-lg text-teal-04">
                check_circle
              </span>
            </div>
          </template>
        </Select>
        <small
          class="text-red-base block mb-4"
          v-for="(error, errorIndex) of validation.pipeline.$errors"
          :key="errorIndex"
          >{{ error.$message }}.
        </small>
      </div>
    </form>
    <div class="space-y-2">
      <p>
        Alternatively, you can refer to the detailed installation instructions below.
      </p>
      <div v-for="tutorial in tutorials" :key="tutorial.title" class="border border-grey-01 rounded-xl bg-grey-01 bg-opacity-30">
        <div class="px-8 py-6 cursor-pointer" @click=" tutorial.is_collapsed = !tutorial.is_collapsed">
          <div class="flex items-center justify-between">
            <span class="font-semibold">
              {{tutorial.title}}
            </span>
            <i
              :class="[
                'material-icons icon-xs',
                'transform transition-all',
                'ease-in-out duration-300',
                tutorial.is_collapsed && 'rotate-180',
                !tutorial.is_collapsed && 'rotate-0'
              ]">
              expand_more
            </i>
          </div>
        </div>
        <Transition name="slide-fade">
          <div v-if="tutorial.is_collapsed" class="px-8 py-6 space-y-4">
            <ul class="space-y-4">
              <li
                v-for="(tutorialStep, n) in tutorial.steps"
                :key="n">
                <div class="flex items-start xl:gap-x-4 gap-x-2">
                  <div class="list-number">{{ n + 1 }}</div>
                  <div
                    :class="[
                      'mt-1 flex-1',
                      tutorialStep.image && 'space-y-2'
                    ]">
                    <p>{{ tutorialStep.text }}</p>
                    <img
                      v-if="!!tutorialStep.image"
                      :src="tutorialStep.image"
                      :alt="`tutorial-step-${n + 1}`"
                      :style="{
                        maxWidth: tutorialStep.width,
                        width: '100%'
                      }"
                      class="w-100"
                    />
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </Transition>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import { useRoute, useRouter } from 'vue-router'
import useGHL from 'composable/integration/ghl'

import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

import Select from 'components/Select.vue'
import CollapsibleCard from 'components/CollapsibleCard.vue'

export default defineComponent({
  components: {
    Button,
    Input,
    Select,
    CollapsibleCard
  },
  async setup(props) {
    const route = useRoute()
    const router = useRouter()
    const isCollapse = ref(false)
    const isInstalled = computed(() => !!status.value?.is_ready)
    const {
      tutorials,
      subAccounts,
      pipelines,
      claimed,
      status,
      query,
      form,
      code,
      loading,
      setSubAccounts,
      setPipelines,
      setClaims,
      updateGHL,
      init
    } = useGHL()
    const validation = useVuelidate({
      subAccount: { required },
      pipeline: { required }
    }, form, {
      $lazy: true
    })
    const handleChangeSub = async(subAccount) => {
      form.subAccount = { ...subAccount }
      await setPipelines({
        location_id: subAccount._id
      })
    }
    const handleChangePipe = (pipeline) => {
      form.pipeline = {
        ...pipeline,
        pipeline_id: pipeline.stages?.length === 1 && [...pipeline.stages]?.shift()?.id
      }
    }
    const handleClaims = async(code) => {
      await setClaims(code)

      if (!!claimed?.value) await init()
      loading.subAccount = true
      if (!!isInstalled.value) await setSubAccounts()
    }
    const saveChanges = async(callback = () => {}) => {
      const isValid = await validation.value.$validate()
      if (!!isValid) {
        return await updateGHL(form.pipeline?.id || form.pipeline?.pipeline_id || form.pipeline).then(() => {
          callback('success')
        }).catch(() => callback('failed'))
      } else {
        return callback('failed')
      }
    }
    const closePanel = () => {
      router.push({
        name: 'integration',
        query: {
          reload: 1
        }
      })
    }

    await init()

    if (route?.query?.code && !isInstalled.value) {
      await handleClaims(route?.query?.code || status.value?.code)
    } else if (!!isInstalled.value) {
      if (!!route?.query?.code) {
        router.replace({
          query: ''
        })
      }

      await setSubAccounts()
      if (status.value?.location_id){
        const subAccSelected = !!Array.isArray(subAccounts.value) && [...subAccounts.value].find(sub => sub._id == status.value.location_id)
       !!subAccSelected && await handleChangeSub(subAccSelected)

        const pipeSelected = !!Array.isArray(pipelines.value) && [...pipelines.value].find(pipe => pipe.id == status.value.pipeline_id)
        !!pipeSelected &&  handleChangePipe(pipeSelected)
      }
    }

    return {
      handleChangeSub,
      handleChangePipe,
      handleClaims,
      saveChanges,
      closePanel,
      validation,
      isCollapse,
      subAccounts,
      isInstalled,
      pipelines,
      tutorials,
      claimed,
      loading,
      status,
      query,
      form,
      code,
      init
    }
  }
})
</script>

<style lang="scss" scoped>
 .list-number {
    min-width: 2rem;
    max-width: 2rem;
    @apply w-8 h-8 bg-teal-04 text-white rounded-full text-center inline-flex items-center justify-center mr-4;
 }
</style>